import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    wallet: {
        address: undefined,
        chain: undefined,
        type: undefined,
        coineusInfo: undefined,
        balance: undefined,
        avax_balance: undefined,
        fusd_balance: undefined,
        usdc_balance: undefined,
        fook_balance: undefined
    },
    prices: {
        avax: undefined,
        fuse: undefined,
        cns: undefined,
        fusd: undefined,
        usdc: undefined,
        fook: undefined,
        fUSDcontract: '0x249be57637d8b013ad64785404b24aebae9b098b',
        fUSDFUSEpair: '0x933a10d094592eb3f2a26bcb366472eba8868a66',
        CNSFUSEpair: '0x0f2e60b799f2237633754f8eec7970c125e15d75',
        USDCAVAXpair: '0xf4003f4efbe8691b60249e6afbd307abe7758adb',
        FOOKAVAXpair: '0x3433b681141B4506B71AEAd3120b264bd9c12863'
    }
};

const coineusSlice = createSlice({
    name: 'coineus',
    initialState,
    reducers: {

        updateWalletChain(state, action) {
            state.wallet.chain = action.payload
        },

        updateWalletAddress(state, action) {
            state.wallet.address = action.payload
        },

        updateWalletProviderType(state, action) {
            state.wallet.type = action.payload
        },

        updateWalletCoineusInfo(state, action) {
            state.wallet.coineusInfo = action.payload
        },

        updateWalletBalance(state, action) {
            state.wallet.balance = action.payload
        },

        updateWalletAVAXBalance(state, action) {
            state.wallet.avax_balance = action.payload
        },

        updateWalletFUSDBalance(state, action) {
            state.wallet.fusd_balance = action.payload
        },

        updateWalletUSDCBalance(state, action) {
            state.wallet.usdc_balance = action.payload
        },

        updateWalletFOOKBalance(state, action) {
            state.wallet.fook_balance = action.payload
        },

        resetWallet(state) {
            state.wallet.chain = undefined;
            state.wallet.address = undefined;
            state.wallet.type = undefined;
            state.wallet.coineusInfo = undefined;
        },

        updatePrices(state, action) {
            state.prices[action.payload.name] = action.payload.price;
        }

    }
});

export const {
    updateWalletChain,
    updateWalletAddress,
    updateWalletProviderType,
    updateWalletCoineusInfo,
    updateWalletBalance,
    updateWalletAVAXBalance,
    updateWalletFUSDBalance,
    updateWalletUSDCBalance,
    updateWalletFOOKBalance,
    resetWallet,
    updatePrices
} = coineusSlice.actions;

export default coineusSlice.reducer;
